const unformatWithPattern = (value, pattern) => {
  if (!value || value === '') {
    return '';
  }

  const valueArray = value.toString().split('');
  const patternDivider = pattern.split('').filter(symbol => symbol !== '*');

  valueArray.forEach((curr, index) => {
    // Remove divider pattern
    if (patternDivider.indexOf(curr) > -1) {
      delete valueArray[index];
    }
  });

  return valueArray.join('');
};

export default unformatWithPattern;



