import { getSymbolsInSelection, getNextGroupedSymbols } from '../';

const pushCursorToStack = (
  valueStack,
  key,
  selectionStart,
  selectionEnd,
  pattern,
  pastedLength = null,
  cursorChar = '|',
) => {
  const newStack = [...valueStack];
  let cursorPosition = selectionStart - getSymbolsInSelection(0, selectionStart, pattern);
  let isDeletion = 0;
  switch (key) {
    case 'Delete':
    case 'Cut':
      break;

    case 'Backspace':
      // If not multiple delete.
      if (selectionStart === selectionEnd) {
        cursorPosition -= 1;
        if (getNextGroupedSymbols(selectionStart, pattern, 'reverse')) {
          isDeletion = 1;
        }
      }
      break;

    case 'Paste':
      cursorPosition += pastedLength;
      break;

    default:
      cursorPosition += 1;
      break;
  }

  newStack.splice(cursorPosition, isDeletion, cursorChar);
  return newStack;
};

export default pushCursorToStack;



