import React from 'react';
import Types from 'prop-types';

import {
  formatWithPattern,
  HistoryNavigator,
  pushCursorToStack,
  unformatWithPattern,
} from '../common/textFormat';

class InputWithPattern extends React.Component {
  static propTypes = {
    pattern: Types.string,
    className: Types.string,
    placeholder: Types.string,
  };

  static defaultProps = {
    className: '',
    pattern: '',
    placeholder: '',
  };

  constructor(props) {
    super(props);
    // Can be anything with at least two chars.
    this.controlChar = '--';
    this.input = React.createRef();
    this.state = {
      value: '',
      isCutting: false,
      isPasting: false,
      isRedo: false,
      isUndo: false,
      pastedLength: 0,
      historyNavigator: new HistoryNavigator(),
      prevPattern: props.pattern,
    };
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    if (prevState.prevPattern !== nextProps.pattern) {
      const { value, prevPattern, historyNavigator } = prevState;
      const { pattern } = nextProps;
      const unFormattedValue = unformatWithPattern(value, prevPattern);
      const formattedValue = formatWithPattern(unFormattedValue, pattern);
      historyNavigator.reset();

      return {
        prevPattern: pattern,
        value: formattedValue,
        isCutting: false,
        isPasting: false,
        isUndo: false,
        isRedo: false,
        pastedLength: 0,
      };
    }
    return null;
  }

  setCursorPosition = select => {
    setTimeout(() => {
      this.input.current.selectionStart = select;
      this.input.current.selectionEnd = select;
    }, 0);
  };

  handleOnKeyDown = e => {
    const { historyNavigator } = this.state;
    const { selectionStart, selectionEnd } = this.input.current;

    const charCode = String.fromCharCode(e.which).toLowerCase();

    if ((e.ctrlKey || e.metaKey) && charCode === 'z') {
      if (e.shiftKey) {
        this.setState({ value: historyNavigator.getHistory('redo'), isUndo: true });
      } else {
        this.setState({ value: historyNavigator.getHistory('undo'), isRedo: true });
      }
    }

    this.setState({ lastKey: e.key, selectionStart, selectionEnd });
  };

  handleOnPaste = e => {
    let pastedChar = e.clipboardData.getData('Text').split('');
    const { pattern } = this.props;
    // Remove symbols from cut text.
    pastedChar = pastedChar.filter(el => pattern.split('').indexOf(el) === -1);

    this.setState({ isPasting: true, pastedLength: pastedChar.length });
  };

  handleOnCut = () => {
    this.setState({ isCutting: true });
  };

  resetEvents = () => {
    this.setState({
      isCutting: false,
      isPasting: false,
      isUndo: false,
      isRedo: false,
      pastedLength: 0,
    });
  };

  handleOnChange = e => {
    const { isRedo, isUndo } = this.state;
    if (isUndo || isRedo) {
      this.resetEvents();
      return;
    }
    const { value } = e.target;
    const { pattern } = this.props;
    const { historyNavigator } = this.state;

    const unformattedValue = unformatWithPattern(value, pattern);

    this.handleCursor(unformattedValue);

    const formattedValue = formatWithPattern(unformattedValue, pattern);

    this.setState(
      { value: formattedValue },
      this.resetEvents(),
      historyNavigator.setHistory(formattedValue),
    );
  };

  handleCursor = stackUnformatted => {
    const { pattern } = this.props;
    const {
      pastedLength,
      selectionEnd,
      selectionStart,
      isPasting,
      isCutting,
      lastKey,
    } = this.state;

    let newKey = lastKey;
    if (isPasting) {
      newKey = 'Paste';
    }
    if (isCutting) {
      newKey = 'Cut';
    }

    const stackUnformattedWithCursor = pushCursorToStack(
      stackUnformatted,
      newKey,
      selectionStart,
      selectionEnd,
      pattern,
      pastedLength,
      this.controlChar,
    );

    const stackFormattedWithCursor = formatWithPattern(stackUnformattedWithCursor, pattern);

    this.setCursorPosition(stackFormattedWithCursor.indexOf(this.controlChar));
  };

  render() {
    const { placeholder, className } = this.props;
    const { value } = this.state;

    return (
      <input
        ref={this.input}
        className={className}
        value={value}
        placeholder={placeholder}
        onPaste={e => this.handleOnPaste(e)}
        onKeyDown={e => this.handleOnKeyDown(e)}
        onChange={e => this.handleOnChange(e)}
        onCut={() => this.handleOnCut()}
      />
    );
  }
}

export default InputWithPattern;



